@import url(https://fonts.googleapis.com/css?family=Economica|Indie+Flower);
.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  font-family: "Economica", sans-serif;
  margin: auto;
  margin-top: 20px;
  width: 500px;
  border: 5px solid #72b148;
  border-radius: 5px;
  overflow-y: hidden;
}

.title {
  font-size: 50px;
  font-weight: 900;
  padding-bottom: 20px;
}

.btn-large {
  width: 400px;
  display: flex;
  margin-top: 20px;
  margin-bottom: 20px;
  text-align: center;
}

.has-shadow {
  box-shadow: -5px 3px 35px -2px rgba(111, 179, 66, 1);
}

.form-title {
  letter-spacing: 1.2px;
  font-weight: 600;
  font-size: 17px;
  padding: 5px 10px;
  position: relative;
  margin-bottom: 20px;
}

.form-title:after {
  content: "";
  position: absolute;
  top: 100%;
  left: 10px;
  width: 95%;
  height: 4px;
  background-color: #72b148;
  border-radius: 50%;
}

.triangle-type-text {
  font-size: 26px;
  font-weight: 700;
  letter-spacing: 2px;
  font-family: "Indie Flower", cursive;
  margin-bottom: 20px;
}

